export default {
	data() {
		return {
			wifis:[],
			index: 0,
		}
	},
	onLoad() {
		uni.setNavigationBarTitle({
		　　title:'设备配网'
		});
		let _this = this;
		wx.startWifi({
			success:res=>{
				console.log(res);
				wx.getWifiList({
					success:res=>{
						console.log(res);
						wx.onGetWifiList(res=>{
							_this.wifis.push(...res.wifiList);
						})
					},
					fail:res=>{
						console.log(res);
					}
				})
			}
		})
		
	},
	methods: {
		bindPickerChange(e){
			this.index = parseInt(e.detail.value);
		},
		next(){
			uni.navigateTo({
				url:'./connectStatus'
			})
		}
	}
}
